[data-class="editable"] {
  position: relative;
  overflow: hidden;
  cursor: pointer;
  width: 100%;
  height: 800px;
  border: 1px solid black; }
  [data-class="editable"] .headerBuild {
    text-align: center;
    font-size: 10vw;
    color: red; }

[data-class="production"] {
  position: relative;
  cursor: auto;
  width: 100%;
  height: 800px;
  border: 1px solid black; }
  [data-class="production"] .headerBuild {
    text-align: center;
    font-size: 10vw;
    color: red; }

.warningEdit {
  cursor: pointer;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  background: gray;
  width: 100%;
  height: 100%;
  top: 0;
  font-size: 3rem;
  color: red;
  margin: 0;
  opacity: .7;
  user-select: none; }
  .warningEdit p {
    font-weight: bold; }

@media screen and (max-width: 700px) {
  .warningEdit {
    text-align: center;
    width: 100%; } }
